.profile__info {
   display: grid;

   grid-template-columns: repeat(2, max-content);
   grid-template-rows: 1fr auto;

   gap: 16px 18px;

   padding: 0 20px 0 30px;
}

@media screen and (max-width: 768px) {
   .profile__info {
      padding: 26px 0 36px 0;
      gap: 14px 10px;
   }
}