.popup {
   visibility: hidden;
   opacity: 0;
   transition: visibility .2s, opacity .2s linear;

   display: flex;
   position: fixed;
   top: 0;
   left: 0;
   right: 0;
   bottom: 0;

   width: 100%;
   height: 100%;

   justify-content: center;
   align-items: center;

   background-color: rgba(0, 0, 0, 0.5);
}